html, body {
  line-height: 1.5;
  background-color: #FFECCF;
  color: #201E1D;
  height: 100%;
  margin: 0;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1, h2, h3 {
  font-family: Consolas, Menlo, 'Andale Mono', 'Roboto Mono', monospace;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FF914D;
  padding: 5px 100px; /* Adjust padding as needed */
}

.logo img {
  vertical-align: middle;
  width: 100px; 
  height: 100px; 
}

.navbar {
  color: #303C6C;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 0;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.navbar li {
  margin: 0 10px;
}

.navbar a {
  text-decoration: none;
  color: white;
  padding: 10px 20px;
}

main {
  flex: 1;
  /* background-color: #B4DFE5; */
  padding: 20px;
  border-radius: 8px;
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  padding-bottom:60px;
}

footer {
  background-color: #AB683E;
  color: white;
  text-align: center;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
}

footer p {
  margin: 0 1px; 
}

.link-button {
  display: flex;
  color: white;
  padding: 6px 10px;
}

.link-button img {
  vertical-align: middle;
  width: 30px; 
  height: 30px; 
}

.container {
  font-family: Arial, sans-serif;
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #D8C3A5;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
input[type="submit"] {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}
input[type="submit"]:hover {
  background-color: #45a049;
}

a:visited {
  color: #6E260E; /* Same as the default link color */
}

a {
  color: #6E260E; /* Same as the default link color */
  text-decoration: underline; /* Underline style for visited links */
  font-weight: bold; /* Make the text bold for visited links */
  transition: color 0.3s ease; /* Smooth transition for color change */
}

a:hover {
  color: #FF5733; /* Change color on hover */
}

.dates {
  font-weight: bold;
  color: #555;
}

.about-me {
  background-color: ivory;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
.about-me h1, .about-me h3, .about-me p {
  color: #333;
}
.about-me p {
  line-height: 1.6;
}
.about-me em {
  font-style: italic;
  font-weight: bold;
}

.section-title {
  text-align: center;
  text-decoration: underline;
  font-weight: bold;          
  /* text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); */
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

.typing-animation {
  overflow: hidden; /* Ensures text is hidden until animation starts */
  white-space: nowrap; /* Prevents text from wrapping */
  animation: typing 3s steps(30, end); /* Adjust duration and steps as needed */
}